<script>
import { validationMixin } from 'vuelidate'
const {required, minLength, maxLength, helpers } = require("vuelidate/lib/validators");

const date = new Date();
const curr_date = date.getDate();
const curr_month = date.getMonth() + 1;
const curr_year = date.getFullYear();

export default {
    name: "Document",
    mixins: [validationMixin],
    props: {
        page: Number
    },
    data() {
        return {
            documentType: '–ü–∞—Å–ø–æ—Ä—Ç',
            documents: ["–ü–∞—Å–ø–æ—Ä—Ç", "–°–≤–∏–¥–µ—Ç–µ–ª—å—Å—Ç–≤–æ –æ —Ä–æ–∂–¥–µ–Ω–∏–∏", "–í–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–µ —É–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–µ"],
            maxDateFormat: `${curr_year}-${curr_month <= 9 ? "0" + curr_month : curr_month}-${curr_date <= 9 ? "0" + curr_date : curr_date}`,
            minDateFormat: '1900-01-01',
            romanNumerals: ['I', 'II', 'III'],

            pasport: {
                series: '',
                number: ''
            },

            birth–°ertificate: {
                series: '',
                cyrillic: '',
                number: ''
            },


            driversLicense: {
                series: '',
                number: ''
            },


            dateOfIssue: '',
            issued: ''
        }
    },
    validations: {
        pasport: {
            series: {
                minLength: minLength(4),
                maxLength: maxLength(4) 
            },

            number: {
                minLength: minLength(6),
                maxLength: maxLength(6) 
            }
        },

        birth–°ertificate: {
            series: {
                minLength: minLength(4),
                maxLength: maxLength(4) 
            },

            cyrillic: {
                minLength: minLength(2),
                maxLength: maxLength(2),
                isCyrillic: helpers.regex('isCyrillic', /[–∞-—è–ê-–Ø—ë–Å]/)
            },

            number: {
                minLength: minLength(6),
                maxLength: maxLength(6) 
            }
        },

        driversLicense: {
            series: {
                minLength: minLength(4),
                maxLength: maxLength(4) 
            },

            number: {
                minLength: minLength(6),
                maxLength: maxLength(6) 
            }
        },

        issued: {
            minLength: minLength(3),
            maxLength: maxLength(30) 
        },

        dateOfIssue: {
            required, 
            dateValidate(value) {
                const [year, mounth, date] = value.split("-");
                return year < 1900 || year > curr_year || (year === 1900 && mounth > 1 && date > 1) ? false : true;
            },
        }

    },

    methods: {
        changeButton(button) {
            this.$emit('changeButton', button, {
                documentType: this.documentType,
                dateOfIssue: this.dateOfIssue,
                issued: this.issued,
                document: this.documentType === '–ü–∞—Å–ø–æ—Ä—Ç' ? this.pasport : this.documentType === '–°–≤–∏–¥–µ—Ç–µ–ª—å—Å—Ç–≤–æ –æ —Ä–æ–∂–¥–µ–Ω–∏–∏' ? this.birth–°ertificate : this.documentType === '–í–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–µ —É–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–µ' ? this.driversLicense : null,
            }, true)
        },
    }
}
</script>

