        placeholder="–ò–º—è*" 
        v-model.trim="$v.name.$model"/>

        <span class="error" v-if="$v.name.$dirty && !$v.name.required">–≠—Ç–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ</span>
        <span class="error" v-if="$v.name.$dirty && !$v.name.maxLength">–ò–º—è –Ω–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –±–æ–ª—å—à–µ –¥–≤–∞–¥—Ü–∞—Ç–∏ –±—É–∫–≤</span>
        <span class="error" v-if="$v.name.$dirty && !$v.name.minLength">–ò–º—è –¥–æ–ª–∂–Ω–æ –∏–º–µ—Ç—å –±–æ–ª—å—à–µ –¥–≤—É—Ö –±—É–∫–≤</span>
        <span class="error" v-if="$v.name.$dirty && !$v.name.isLetter">–ò–º—è –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ —Ä—É—Å—Å–∫–∏–µ –∏–ª–∏ –∞–Ω–≥–ª–∏–π—Å–∫–∏–µ –±—É–∫–≤—ã –±–µ–∑ —Å–ø–µ—Ü —Å–∏–º–≤–æ–ª–æ–≤ –∏ –ø—Ä–æ–±–µ–ª–æ–≤</span>
      </div>

      <div class="form_group">
        <label>–§–∞–º–∏–ª–∏—è*</label>
        <input 
        type="text" 
        :class="$v.surname.$dirty && $v.surname.$invalid ? 'invalid' : 'valid'" 
        placeholder="–§–∞–º–∏–ª–∏—è*" 
        v-model.trim="$v.surname.$model"/>

        <span class="error" v-if="$v.surname.$dirty && !$v.surname.required">–≠—Ç–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ</span>
        <span class="error" v-if="$v.surname.$dirty && !$v.surname.maxLength">–§–∞–º–∏–ª–∏—è –Ω–µ –¥–æ–ª–∂–Ω–∞ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –±–æ–ª—å—à–µ –¥–≤–∞–¥—Ü–∞—Ç–∏ –±—É–∫–≤</span>
        <span class="error" v-if="$v.surname.$dirty && !$v.surname.minLength">–§–∞–º–∏–ª–∏—è –¥–æ–ª–∂–Ω–∞ –∏–º–µ—Ç—å –±–æ–ª—å—à–µ –¥–≤—É—Ö –±—É–∫–≤</span>
        <span class="error" v-if="$v.surname.$dirty && !$v.surname.isLetter">–§–∞–º–∏–ª–∏—è –¥–æ–ª–∂–Ω–∞ —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ —Ä—É—Å—Å–∫–∏–µ –∏–ª–∏ –∞–Ω–≥–ª–∏–π—Å–∫–∏–µ –±—É–∫–≤—ã –±–µ–∑ —Å–ø–µ—Ü —Å–∏–º–≤–æ–ª–æ–≤ –∏ –ø—Ä–æ–±–µ–ª–æ–≤</span>
      </div>

      <div class="form_group">
        <label>–û—Ç—á–µ—Å—Ç–≤–æ</label>
        <input 
        type="text" 
        :class="$v.patronymic.$dirty && $v.patronymic.$invalid ? 'invalid' : 'valid'" 
        placeholder="–û—Ç—á–µ—Å—Ç–≤–æ" 
        v-model.trim="$v.patronymic.$model"/>

        <span class="error" v-if="$v.patronymic.$dirty && !$v.patronymic.maxLength">–û—Ç—á–µ—Å—Ç–≤–æ –Ω–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –±–æ–ª—å—à–µ –¥–≤–∞–¥—Ü–∞—Ç–∏ –±—É–∫–≤</span>
        <span class="error" v-if="$v.patronymic.$dirty && !$v.patronymic.minLength">–û—Ç—á–µ—Å—Ç–≤–æ –¥–æ–ª–∂–Ω–æ –∏–º–µ—Ç—å –±–æ–ª—å—à–µ –¥–≤—É—Ö –±—É–∫–≤</span>
        <span class="error" v-if="$v.patronymic.$dirty && !$v.patronymic.isLetter">–û—Ç—á–µ—Å—Ç–≤–æ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ —Ä—É—Å—Å–∫–∏–µ –∏–ª–∏ –∞–Ω–≥–ª–∏–π—Å–∫–∏–µ –±—É–∫–≤—ã –±–µ–∑ —Å–ø–µ—Ü —Å–∏–º–≤–æ–ª–æ–≤ –∏ –ø—Ä–æ–±–µ–ª–æ–≤</span>
      </div>

      <div class="form_group">
        <label>–î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è*</label>
        <input 
        type="date" 
        :class="$v.birthDay.$dirty && $v.birthDay.$invalid ? 'invalid' : 'valid'" 
        :min="minDateFormat"
        :max="maxDateFormat"
        v-model.trim="$v.birthDay.$model"/>

        <span class="error" v-if="$v.birthDay.$dirty && !$v.birthDay.required">–≠—Ç–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ</span>
        <span class="error" v-if="$v.birthDay.$dirty && !$v.birthDay.dateValidate">–ù–µ–≤–∞–ª–∏–¥–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ</span>
      </div>

      <div class="form_group">
        <label>–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞*</label>
        <input 
        type="tel" 
        :class="$v.phone.$dirty && $v.phone.$error ? 'invalid' : 'valid'" 
        placeholder="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞*" 
        v-model.trim="$v.phone.$model"
        @focus="setPhoneCode"
        @input="setPhoneCode"
        />

        <span class="error" v-if="$v.phone.$dirty && !$v.phone.required">–≠—Ç–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ</span>
        <span class="error" v-if="$v.phone.$dirty && !$v.phone.isNumber">–¢–µ–ª–µ—Ñ–æ–Ω –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –≤ —Å–µ–±–µ —Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä—ã</span>
        <span class="error" v-if="$v.phone.$dirty && !$v.phone.lengthValidate">–î–æ–ª–∂–Ω–æ –±—ã—Ç—å –¥–µ—Å—è—Ç—å —Ü–∏—Ñ—Ä –ø–æ—Å–ª–µ <b>"+7"</b></span>
      </div>

      <div class="form_group">
        <label>–ì—Ä—É–ø–ø–∞ –∫–ª–∏–µ–Ω—Ç–æ–≤*</label>
        <select :class="$v.clientGroupUser.$dirty && $v.clientGroupUser.$error ? 'invalid' : 'valid'" v-model="$v.clientGroupUser.$model" multiple> 
        <option v-for="group in clientGroup" :key="group">{{ group }}</option>
        </select>

        <span class="error" v-if="$v.clientGroupUser.$dirty && !$v.clientGroupUser.required">–≠—Ç–æ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ</span>
      </div>

      <div class="form_group">
        <label>–õ–µ—á–∞—â–∏–π –≤—Ä–∞—á</label>
        <select class="valid" v-model="doctorUser"> 
            <option v-for="doctor in doctors" :key="doctor">{{ doctor }}</option>
        </select>
      </div>

      <div class="form_group">
        <label>–ù–µ –æ—Ç–ø—Ä–∞–≤–ª—è—Ç—å –°–ú–°</label>
        <input type="checkbox" v-model="doNotSendSMS">
      </div>

      <div class="buttons">
        <button :disabled="page === 1 || $v.$invalid" @click="changeButton('buttonBack')"> –ù–∞–∑–∞–¥</button>
        <button :disabled="$v.$invalid" @click="changeButton('buttonNext')"> –í–ø–µ—Ä–µ–¥ </button>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
const {
  required,
  minLength,
  maxLength,
  helpers
} = require("vuelidate/lib/validators");

const date = new Date();
const curr_date = date.getDate();
const curr_month = date.getMonth() + 1;
const curr_year = date.getFullYear();


export default {
    name: 'Basic',
    mixins: [validationMixin],
    props: {
        page: Number
    },
    data() {
        return {
            name: '',
            surname: '',
            patronymic: '',
            birthDay: '',
            phone: "",
            clientGroupUser: ["VIP"],
            doctorUser: '–ù–∏–∫—Ç–æ',            
            doNotSendSMS: false,

            maxDateFormat: `${curr_year}-${curr_month <= 9 ? "0" + curr_month : curr_month}-${curr_date <= 9 ? "0" + curr_date : curr_date}`,
            minDateFormat: '1900-01-01',
            clientGroup: ["VIP", "–ü—Ä–æ–±–ª–µ–º–Ω—ã–µ", "–û–ú–°"],
            doctors: ['–ù–∏–∫—Ç–æ', "–ò–≤–∞–Ω–æ–≤", "–ó–∞—Ö–∞—Ä–æ–≤", "–ß–µ—Ä–Ω—ã—à–µ–≤–∞"]
        }
    },
    methods: {
        setPhoneCode() {
            const [plus, code] = this.phone.split('')
            if(plus !== '+' || code !== '7') {
                this.phone = '+7' + ""
            }
            
        },
        changeButton(button) {
            this.$emit('changeButton', button, {
                name: this.name,
                surname: this.surname,
                patronymic: this.patronymic,
                birthDay: this.birthDay,
                phone: this.phone,
                clientGroupUser: this.clientGroupUser,
                doctorUser: this.doctorUser,      
                doNotSendSMS: this.doNotSendSMS
            })
        }
    },
    validations: {
            name: {
                required,
                minLength: minLength(2),
                maxLength: maxLength(20),
                isLetter: helpers.regex('isLetter', /^[a-zA-Z–∞-—è–ê-–Ø—ë–Å]*$/)
            },

            surname: {
                required,
                minLength: minLength(2),
                maxLength: maxLength(20),
                isLetter: helpers.regex('isLetter', /^[a-zA-Z–∞-—è–ê-–Ø—ë–Å]*$/)
            },

            patronymic: {
                minLength: minLength(2),
                maxLength: maxLength(20),
                isLetter: helpers.regex('isLetter', /^[a-zA-Z–∞-—è–ê-–Ø—ë–Å]*$/)
            },

            birthDay: {
                required,
                dateValidate(value) {
                    const [year, mounth, date] = value.split("-");
                    return year < 1900 || year > curr_year || (year === 1900 && mounth > 1 && date > 1) ? false : true;
                },
            },

            phone: {
                required(value) {
                    let copy = [...value]
                    delete copy[0]
                    delete copy[1]
                    return copy.filter(item => item).length === 0 ? false : true 
                },
                isNumber(value) {
                    let copy = value.split('')
                    delete copy[0] 
                    let str = copy.filter(item => item).join('');
                    let regexp = /^[0-9]+$/;

                    return regexp.test(str)
                },
                lengthValidate(value) {
                    let copy = [...value]
                    delete copy[0]
                    delete copy[1]
                    return copy.filter(item => item).length !== 10 ? false : true 
                }
            },

            clientGroupUser: {
                required
            }
        
    },
}
</script>

